<template>
  <div class="main-content">
    <!--menu-->
    <el-row>
      <el-col :span="14">
        <common-wrapper v-loading="!tableVisible">
          <common-table :flex="170" :minHeight="400">
            <template slot="table">
              <el-table ref="table" :data="tableData" :default-sort = "{prop: 'registerTime', order: 'descending'}" stripe>
                <el-table-column prop="orgName" label="所属公司" ></el-table-column>
                <el-table-column prop="lineName" label="线路名称" ></el-table-column>
                <el-table-column prop="plateNum" label="车牌号" ></el-table-column>
                <el-table-column prop="warnType" label="监测类型" ></el-table-column>
                <el-table-column  label="地图定位">
                  <template slot-scope="scope">
                          <el-popover
                            placement="right"
                            trigger="click"
                            >
                            <baidu-map-com :row="scope.row" :styleWH="styleWH"></baidu-map-com>
                            <el-button  icon="el-icon-location-outline" circle slot="reference" ></el-button>
                          </el-popover>
                      </template>
                </el-table-column>
                <el-table-column prop="createTime" label="触发时间" min-width="180"></el-table-column>
                <el-table-column  label="查看">
                  <template slot-scope="scope">
                          <el-button  icon="el-icon-view" round @click="handleLineData(scope.row.warnUuid)"></el-button>
                      </template>
                </el-table-column>
              </el-table>
            </template>
            <template slot="pagination">
              <el-pagination
              background
              :current-page="currentPage"
              :page-size="pageCount"
              layout="total, prev, pager, next"
              :total="totalCount"
              @current-change="handleCurrentPage">
              </el-pagination>
            </template>
          </common-table>
        </common-wrapper>
      </el-col>
      <el-col :span="10">
         <common-wrapper class="commonH">
            <el-row>
              <el-col :span="24">
                <div class="list-box">
                  <ul class="list-block">
                    <li>司机姓名</li><li>{{driverData.driverName}}</li>
                  </ul>
                  <ul class="list-block">
                    <li>当前线路</li><li>{{driverData.lineName}}</li>
                  </ul>
                  <ul class="list-block">
                    <li>所属公司</li><li>{{driverData.driverOrg}}</li>
                  </ul>
                </div>
              </el-col>
              <el-col :span="24">
                <div class="player">
                    <video :src="videoOpts.src" :poster="videoOpts.poster"
                     :controls="videoOpts.controls" :preload="videoOpts.preload"
                     :autoplay="videoOpts.autoplay" :loop="videoOpts.loop"
                     :muted="videoOpts.muted" :width="videoOpts.width">
                      您的浏览器不支持 video 标签。
                    </video>
                </div>
              </el-col>
            </el-row>
          </common-wrapper>
      </el-col>
    </el-row>
  </div>
</template>
<script>
// 自定义组件
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import BaiduMapCom from '../../Common/PositionMap/index'
// api
import { warndetailData, warndetailList } from '@/api/device'

export default {
  name: 'DriverDetail',
  props: {
    row: Object
  },
  data () {
    return {
      tableVisible: false,
      tableData: [], // 表格当前页数据
      pageCount: 10, // 默认每页数据量
      currentPage: 1, // 当前页码
      totalCount: null, // 数据总数
      driverData: {
        driverName: '',
        lineName: '',
        driverOrg: ''
      },
      styleWH: {
        width: 400,
        height: 400
      },
      videoOpts: {
        src: '',
        poster: '',
        preload: true,
        controls: true,
        muted: false,
        autoplay: false,
        loop: false,
        width: '100%',
        height: ''
      }
    }
  },
  beforeCreate () {

  },
  mounted () {
    this.$nextTick(function () {
      this.getVideoData(this.row.warnUuid).then(res => {
        // console.log(res)
        // console.log(res.url)
        this.videoOpts.src = res.url
        this.videoOpts.poster = res.poster
      })
      this.getTableData()
    })
  },
  methods: {
    // 获取视频数据
    getVideoData (row) {
      return new Promise(resolve => {
        warndetailData({warnUuid: row}).then(res => {
          this.driverData.driverName = res.driverName
          this.driverData.lineName = res.lineName
          this.driverData.driverOrg = res.driverOrg
          resolve(res)
        })
      })
    },
    // 获取表格数据
    getTableData () {
      const params = {
        warnUuid: this.row.warnUuid,
        pageNum: this.currentPage,
        pageSize: this.pageCount
      }
      warndetailList(params).then(res => {
        this.tableVisible = true
        this.tableData = res.list
        this.pageCount = parseInt(res.pageSize)
        this.currentPage = parseInt(res.pageNum)
        this.totalCount = parseInt(res.total)
      })
    },
    // 查看分页数据
    handleCurrentPage (pageNum) {
      this.currentPage = pageNum
      this.getTableData()
    },
    // 查看不同路线的数据
    handleLineData (row) {
      this.getVideoData(row)
    }
  },
  components: {
    CommonWrapper,
    CommonTable,
    BaiduMapCom
  }
}
</script>
<style lang="scss" scoped>
.player{
    width:100%;
    max-width:1000px;
    min-height: 100px;
    max-height: 400px;
    margin: 25px 0;
    color: darken( $base-light-color, 10%);

    .video-player {
        box-shadow: 0 10px 35px rgba(0,0,0,.7)
    }

}
.list-box{
  margin: 25px 0;
}
ul {
  min-width:100px;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  &:nth-child(1) {
   border-top:1px solid darken($base-light-color, 12%);
   border-top-right-radius: 10px;
   border-top-left-radius: 10px;
    li:last-child {
      border-top-right-radius: 10px;
    }
    li:first-child {
      border-left:1px solid darken($base-light-color, 12%);
      border-top-left-radius: 10px;
    }
  }
  &:nth-child(2) {
   border-left:1px solid darken($base-light-color, 12%);
  }
  &:last-child {
    border-left:1px solid darken($base-light-color, 12%);
    border-bottom-left-radius: 10px;
    li:last-child {
      border-bottom-right-radius: 10px;
    }
    li:first-child {
      border-bottom-left-radius: 10px;
    }
  }
  li {
    flex: auto;
    text-align: center;
    border-right:1px solid darken($base-light-color, 12%);
    border-bottom:1px solid darken($base-light-color, 12%);
    box-sizing: border-box;
    padding:15px 10px;
    width: 200px;
  }
}
.commonH {
   min-height: 84vh;
}
</style>



